import { Injectable ,OnInit } from '@angular/core';
import { Http ,Response} from '@angular/http';
import { HttpModule } from '@angular/http';
import { HttpClient,HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Headers } from '@angular/http';
import { Router ,Resolve } from '@angular/router'
import  'rxjs/add/operator/map';
import {ActivatedRoute} from "@angular/router";
import { SessionStorageService } from 'ngx-webstorage';
import { FlashMessage } from 'angular-flash-message/dist';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { CookieService } from 'ngx-cookie-service';

@Injectable()
export class ManageCustomerService implements OnInit{
name:any;
companyID:any;
arrayUser:any;
  constructor(
    private http:Http,
    private route:ActivatedRoute,
    private sessionSt:SessionStorageService,
    public router:Router,
    private flashmessages:FlashMessage,
    private cookieService:CookieService,
    public toastr:ToastsManager,
  ) { }

  ngOnInit() {

  }


  AddCustomersApi(name,company,phone,email){
    console.log(name.value)

      this.companyID=this.sessionSt.retrieve("companyID");

      let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
        let options = { headers, observe: 'response' };
            const body3 = new HttpParams()
             .set('company_id',this.companyID)
             .set('customer_name',name.value.customers_name)
                .set('customer_company_name',name.value.customer_company_name)
                .set('customer_phone_number',name.value.customers_phone)
                .set('customer_email',name.value.customers_email_id)
          return this.http.post("https://thawing-depths-48088.herokuapp.com/add_customer.json",body3.toString(),options).map((response:Response)=>response.json()
      );
  }


  removeCustomersApi(remove){


      this.companyID=this.sessionSt.retrieve("companyID");
      this.arrayUser=this.sessionSt.retrieve("arrayUser");


      let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
        let options = { headers, observe: 'response' };
            const body4 = new HttpParams()
             .set('company_id',this.companyID)
             .set('customers',this.arrayUser)

          return this.http.post("http://thawing-depths-48088.herokuapp.com/delete_customers.json",body4.toString(),options).map((response:Response)=>response.json()
      );
  }
}



