import { BrowserModule } from '@angular/platform-browser';
import { NgModule  } from '@angular/core';
import { HttpModule } from '@angular/http';
import { ReactiveFormsModule} from '@angular/forms';
import { NgSemanticModule } from 'ng-semantic';

import { FormsModule } from '@angular/forms';
import { LoginComponent } from './login/login.component';
import { routes } from './app.routes';
import { AppComponent } from './app.component';
import { SignupComponent } from './signup/signup.component';
import { ServiceService } from './service.service';
import { NguiTabModule } from '@ngui/tab';
import { Ng2Webstorage } from 'ngx-webstorage';
import { DashboardComponent } from './dashboard/dashboard.component';
import { FlashMessageModule } from 'angular-flash-message/dist';
import {ToastModule} from 'ng2-toastr/ng2-toastr';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { CookieService } from 'ngx-cookie-service';
import { DashboardService } from './dashboard.service';
import { ZiggeoModule } from 'angular-ziggeo/build/ziggeo/index';
import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';
import { ManageTeamComponent } from './manage-team/manage-team.component';
import { NevbarComponent } from './nevbar/nevbar.component';
import { NavbarService } from './navbar.service';
import { ManageTeamService } from './manage-team.service';
import { QuestionComponent } from './question/question.component';
import { QuestionService } from './question.service';
import { ManageCustomerComponent } from './manage-customer/manage-customer.component';
import { ManageCustomerService } from './manage-customer.service';
import { CreateJoygramComponent } from './create-joygram/create-joygram.component';
import {  CreateJoygramService } from './create-joygram.service';
import { ZiggeoRecorderService  } from './ziggeo-recorder.service';
import {MatCheckboxModule} from '@angular/material/checkbox';
import {MatSelectModule} from '@angular/material/select';
import {MatChipsModule} from '@angular/material/chips';
import {MatIconModule} from '@angular/material/icon';
import {MatAutocompleteModule} from '@angular/material/autocomplete';
import {MatButtonModule} from '@angular/material/button';
import {MatTableModule} from '@angular/material/table';
import {MatTooltipModule} from '@angular/material/tooltip';
import {MatToolbarModule} from '@angular/material/toolbar';
import {MatInputModule} from '@angular/material/input';
import {MatFormFieldModule} from '@angular/material/form-field';
import { MDBBootstrapModule } from 'angular-bootstrap-md';


import {SuiModule} from 'ng2-semantic-ui';
import {SuiCheckboxModule, SuiRatingModule} from 'ng2-semantic-ui';
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';





@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    SignupComponent,
    DashboardComponent,
    ManageTeamComponent,
    NevbarComponent,
    QuestionComponent,
    ManageCustomerComponent,
    CreateJoygramComponent,

  ],
  imports: [

    NgMultiSelectDropDownModule.forRoot(),
    MatFormFieldModule,
    SuiCheckboxModule,

   SuiRatingModule,
    MatInputModule,
    MatButtonModule,
    MatTooltipModule,
    MatToolbarModule,
    FormsModule,
    HttpModule,
    BrowserModule,
    BrowserAnimationsModule,
    FlashMessageModule,
    Ng2Webstorage ,
    NguiTabModule,
    routes,
    ZiggeoModule,
    NgSemanticModule,
    MatChipsModule,
    MDBBootstrapModule.forRoot(),

    SuiModule,
    ToastModule.forRoot(),
    Ng4LoadingSpinnerModule.forRoot(),
    MatCheckboxModule,
    MatSelectModule,
    MatIconModule,
    ReactiveFormsModule,
    MatAutocompleteModule,

],
providers: [
  MatTableModule,
    ServiceService,
    CookieService,
    DashboardService,
    NavbarService,
    ManageTeamService,
    QuestionService,
    ManageCustomerService,
    CreateJoygramService,
    ZiggeoRecorderService ,
  ],
  bootstrap: [AppComponent]
})
// @NgModule({
//   import:[MatButtonModule,MatCheckboxModule],
//   export:[MatButtonModule,MatCheckboxModule],
// })
export class AppModule { }



