import { Injectable,OnInit } from '@angular/core';
import { Http ,Response} from '@angular/http';
import { HttpModule } from '@angular/http';
import { HttpClient,HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Headers } from '@angular/http';
import { Router ,Resolve } from '@angular/router'
 import  'rxjs/add/operator/map';
import {ActivatedRoute} from "@angular/router";
import { SessionStorageService } from 'ngx-webstorage';
import { FlashMessage } from 'angular-flash-message/dist';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { CookieService } from 'ngx-cookie-service';

// import { CookieService } from 'ngx-cookie-service';

@Injectable()
export class DashboardService {
companyID:any;
Aboutuser:any;
base64Url:any;
showUrl:any;
first_name:any;
last_name:any;
imageSrc:any;
image:any;

  constructor(
    private http:Http,
    private route:ActivatedRoute,
    private sessionSt:SessionStorageService,
    public router:Router,
    private flashmessages:FlashMessage,
      private cookieService:CookieService,
    public toastr:ToastsManager,
    // private cookieService:CookieService,
  ) {  }

ngOnInit(){



}
getAllUsersData(){
  this.companyID=this.sessionSt.retrieve("companyID");


  let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
    let options = { headers, observe: 'response' };
        const body3 = new HttpParams()
         .set('company_id',this.companyID)
      return this.http.post("http://thawing-depths-48088.herokuapp.com/list_of_company_roles.json",body3.toString(),options).map((response:Response)=>response.json().users
  );
}


getAllQuestions(){
  this.companyID=this.sessionSt.retrieve("companyID");


  let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
    let options = { headers, observe: 'response' };
        const body5 = new HttpParams()
         .set('company_id',this.companyID)
      return this.http.post("http://thawing-depths-48088.herokuapp.com/list_of_all_questions.json",body5.toString(),options).map((response:Response)=>response.json().questions
  );
}

getjoygramvideos(){
  this.companyID=this.sessionSt.retrieve("companyID");


  let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
    let options = { headers, observe: 'response' };
        const body6 = new HttpParams()
         .set('company_id',this.companyID)
         .set('page','1')

      return this.http.post("http://thawing-depths-48088.herokuapp.com/get_all_company_joygrams.json",body6.toString(),options).map((response:Response)=>response.json().events
  );
}
ManageCustomers(){
  this.companyID=this.sessionSt.retrieve("companyID");


  let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
    let options = { headers, observe: 'response' };
        const body7 = new HttpParams()
         .set('company_id',this.companyID)

      return this.http.post("http://thawing-depths-48088.herokuapp.com/list_of_customers.json",body7.toString(),options).map((response:Response)=>response.json().customers
  );
  }

logoutApi(){
this.Aboutuser=this.sessionSt.retrieve("aboutuser");


console.log(this.Aboutuser.remember_token);
  let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
    let options = { headers, observe: 'response' };
        const body7 = new HttpParams()
         .set('remember_token',this.Aboutuser.remember_token)

      return this.http.post("http://thawing-depths-48088.herokuapp.com/nb_logout.json",body7.toString(),options).map((response:Response)=>response.json()
  );
}



UpdateProfileApi(){


  this.Aboutuser=this.sessionSt.retrieve("aboutuser");
  this.showUrl=this.sessionSt.retrieve("showUrl");
  this.first_name=this.sessionSt.retrieve("first_name");
  this.last_name=this.sessionSt.retrieve("last_name");


// this.showUrl=this.sessionSt.retrieve("showUrl");

console.log(this.Aboutuser.remember_token);
  let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
    let options = { headers, observe: 'response' };
        const body7 = new HttpParams()
          .set('user_id',this.Aboutuser.user_id)
          // .set('file',this.showUrl)
          .set('file',this.showUrl)
          .set('first_name',this.first_name)
          .set('last_name',this.last_name)

      return this.http.post("https://thawing-depths-48088.herokuapp.com/update_profile.json",body7.toString(),options).map((response:Response)=>response.json()
  );

}




}



