import { Component, OnInit, ViewChild ,ViewContainerRef} from '@angular/core';
import { Http,Headers } from '@angular/http'
import { DashboardService } from '../dashboard.service';
import { Observable } from 'rxjs';
import { HttpClient,HttpParams } from '@angular/common/http';
import {ActivatedRoute } from "@angular/router";
import { SessionStorageService } from 'ngx-webstorage';
import { Router } from '@angular/router';
import { FlashMessage } from 'angular-flash-message/dist';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { CookieService } from 'ngx-cookie-service';
import { ZiggeoModule } from 'angular-ziggeo/build/ziggeo/index';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { ManageTeamService } from '../manage-team.service';
import {  ManageCustomerService } from '../manage-customer.service';


@Component({
  selector: 'app-manage-customer',
  templateUrl: './manage-customer.component.html',
  styleUrls: ['./manage-customer.component.css']
})
export class ManageCustomerComponent implements OnInit {
Aboutuser:any;
manageTeam:any;
AllCustomers:any;
selectedAll: any;
AddCustRes:any;
customerDetails:any;
selected:boolean=true;
userVar:any=[];
arrayUser:any;
removeRes:any;
  constructor(
    private http:Http,
    private dashboardservice:DashboardService,
    private route:ActivatedRoute,
    private sessionSt:SessionStorageService,
    private router:Router,
    private flashmessages:FlashMessage,
    public toastr:ToastsManager,
    private cookieService:CookieService,
    private spinnerService: Ng4LoadingSpinnerService,
    private teamService:ManageTeamService,
    public vcr: ViewContainerRef,
    private managecustomerService:ManageCustomerService,
    )

     {  this.spinnerService.show();
    this.toastr.setRootViewContainerRef(vcr);
}
ngOnInit() {
  this.Aboutuser=this.sessionSt.retrieve("aboutuser");
  this.manageTeam=this.sessionSt.retrieve("allusers");
  this.AllCustomers=this.sessionSt.retrieve("manageCustomerDetails");

}


  ManageCustomer(name,company,phone,email){
console.log(name.value)
      this.managecustomerService.AddCustomersApi(name,company,phone,email).subscribe(
          data=> { this.AddCustRes=data;
            this.AddCustRes=(data);
            console.log(this.AddCustRes);
            this.route.params.subscribe(value => {
            value=this.AddCustRes;
            this.sessionSt.store('AddCustRes',this.AddCustRes)
              if(this.AddCustRes.status==200){

                this.toastr.success(this.AddCustRes.message,"Thank You..." )

                  this.dashboardservice.ManageCustomers().subscribe(
                      data=> { this.customerDetails=data;
                        this.customerDetails=(data)
                        console.log(this.customerDetails);
                        this.route.params.subscribe(value => {
                        value=this.customerDetails;
                        this.sessionSt.store('manageCustomerDetails',this.customerDetails)
                        location.reload();

                      }
                  );
                  this.AllCustomers=this.sessionSt.retrieve("manageCustomerDetails");

                  });




              }
              else{
                this.toastr.info(this.AddCustRes.message,"Sorry..." )
              }


          });

});

}

onItemClick(event,customer){

if(event.target.checked==true){
let IdUser = {"user_id":customer.id}
this.userVar.push(IdUser);
this.arrayUser=JSON.stringify(this.userVar);
console.log(this.arrayUser);
}
else{
  let IdUser = {"user_id":customer.id}

  this.userVar.splice(this.userVar.indexOf(IdUser), 1);
  this.arrayUser=JSON.stringify(this.userVar);
  console.log(this.arrayUser)
}

this.route.params.subscribe(value => {
value=this.arrayUser;
this.sessionSt.store('arrayUser',this.arrayUser)
});

}

removeCustomer(remove){
this.managecustomerService.removeCustomersApi(remove).subscribe(
    data=> { this.removeRes=data;
      this.removeRes=(data)
      console.log(this.removeRes);
      this.route.params.subscribe(value => {
      value=this.removeRes;
      this.sessionSt.store('removeRes',this.removeRes)

    }
);
this.removeRes=this.sessionSt.retrieve("removeRes");
if(this.removeRes.status==200)
{
  this.toastr.success(this.removeRes.message )
  this.dashboardservice.ManageCustomers().subscribe(
      data=> { this.customerDetails=data;
        this.customerDetails=(data)
        console.log(this.customerDetails);
        this.route.params.subscribe(value => {
        value=this.customerDetails;
        this.sessionSt.store('manageCustomerDetails',this.customerDetails)
        location.reload();

      }
  );
  this.AllCustomers=this.sessionSt.retrieve("manageCustomerDetails");

  });

  }
});

}

}



