import { Injectable,OnInit } from '@angular/core';
import { Http ,Response} from '@angular/http';
import { HttpModule } from '@angular/http';
import { HttpClient,HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Headers } from '@angular/http';
import { Router } from '@angular/router';
 import  'rxjs/add/operator/map';
import {ActivatedRoute} from "@angular/router";
import { SessionStorageService } from 'ngx-webstorage';
import { FlashMessage } from 'angular-flash-message/dist';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { CookieService } from 'ngx-cookie-service';

@Injectable()
export class ServiceService implements OnInit {
urlID:any;
authDetails:any;
access_type:any;
gmailDetails:any;
getCompanyDetail:any;
thisID:any;
user:any;
code:any;
gmail:any;
email:any;
userdetails:any;
companyID:any;
name:any;
registerName:any;
registerEmail:any;
companyUrlfromCookie:any;
  constructor(
    private http:Http,
    private route:ActivatedRoute,
    private sessionSt:SessionStorageService,
    public router:Router,
    private flashmessages:FlashMessage,
    public toastr:ToastsManager,
    private cookieService:CookieService

  )
    {


      this.getCompanyDetail=this.sessionSt.retrieve("CompanyDetail");
       this.gmailDetails=this.sessionSt.retrieve("userdetails");

  }

ngOnInit(){
  // this.email=this.sessionSt.retrieve("email");
 window.sessionStorage.clear();


}
  getCompanyData(){
    this.urlID=this.sessionSt.retrieve("company");
    this.companyUrlfromCookie=this.cookieService.get('companyUrl');
    this.getCompanyDetail=this.sessionSt.retrieve("CompanyDetail");

    let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
        let options = { headers, observe: 'response' };

        const body = new HttpParams()
        .set('slug_name', this.companyUrlfromCookie)


  return this.http.post("https://thawing-depths-48088.herokuapp.com/get_company_details.json",body.toString(),options).map((response:Response)=>response.json().company);
}

//Login Process
userAuthenticationLogin(email,access_type,contact){
  this.gmailDetails=this.sessionSt.retrieve("userdetails");
let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
let options = { headers, observe: 'response' };
      const gmail = new HttpParams()
    .set('email', email.value.email)
this.gmail= email.value.email

this.route.params.subscribe(params => {
  this.email = params;
this.sessionSt.store('email',email.value.email)
});

// console.log(this.gmail)
      return this.http.post("http://thawing-depths-48088.herokuapp.com/player_access.json",gmail.toString(),options).map((response:Response)=>response.json()


    );
}
userAuthenticationRegister(name,email,access_type){
  this.gmailDetails=this.sessionSt.retrieve("userdetails");
let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
let options = { headers, observe: 'response' };
      const gmail = new HttpParams()
    .set('email',name.value.email)

      return this.http.post("http://thawing-depths-48088.herokuapp.com/player_access.json",gmail.toString(),options).map((response:Response)=>response.json()


    );
}
//Registration Process


getUserDetails(name,code,gmail,companyId,userdetails){
// console.log(name.value.name)
this.userdetails=this.sessionSt.retrieve("userdetails");
this.companyID=this.sessionSt.retrieve("companyID");
this.registerEmail=this.sessionSt.retrieve("registeremail");
this.registerName=this.sessionSt.retrieve("registername");


  let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
    let options = { headers, observe: 'response' };
        const body2 = new HttpParams()
         .set('email',this.registerEmail)
         .set('access_code',name.value.code)
         // .set('name',name.value.name)
         .set('name',this.registerName)
         .set('company_id',this.companyID)
      return this.http.post("http://thawing-depths-48088.herokuapp.com/user_company_employee.json",body2.toString(),options).map((response:Response)=>response.json()

);

}



}



