import { Component, OnInit } from '@angular/core';
import { NavbarService } from '../navbar.service';
import { Http,Headers } from '@angular/http'
import { DashboardService } from '../dashboard.service';
import { Observable } from 'rxjs';
import { HttpClient,HttpParams } from '@angular/common/http';
import {ActivatedRoute } from "@angular/router";
import { SessionStorageService } from 'ngx-webstorage';
import { Router } from '@angular/router';
import { FlashMessage } from 'angular-flash-message/dist';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { CookieService } from 'ngx-cookie-service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
@Component({
  selector: 'app-nevbar',
  moduleId: module.id,
  templateUrl: './nevbar.component.html',
  styleUrls: ['./nevbar.component.css']
})

export class NevbarComponent implements OnInit {
  CompanyDetail:any;
  companiesUrl:any;
  logoutRes:any;
  constructor(

    private http:Http,
    private dashboardservice:DashboardService,
    private route:ActivatedRoute,
    private sessionSt:SessionStorageService,
    public nav: NavbarService,
    private router:Router,
    private flashmessages:FlashMessage,
    public toastr:ToastsManager,
    private cookieService:CookieService,
    private spinnerService: Ng4LoadingSpinnerService ) {}

  ngOnInit() {
    this.CompanyDetail = this.sessionSt.retrieve("companydetail");
  }


  logout(){

  this.companiesUrl=this.cookieService.get('company');

    this.dashboardservice.logoutApi().subscribe(
        data=> { this.logoutRes=data;
          this.logoutRes=(data)
          console.log(this.logoutRes);
          this.toastr.info(this.logoutRes.message );
             // window.sessionStorage.remove('aboutuser');
             alert(this.logoutRes.message)
             // window.sessionStorage.clear();
  this.toastr.info(this.logoutRes.message );
             setTimeout(() =>
             {
               this.router.navigate(['/login/'+ this.companiesUrl]);
                location.reload();
             },
             1000);

  window.sessionStorage.clear();
    });
  }
}



