// for question array
questionsId(event,q){
  console.log(q.answer_type.id);
if(event.target.checked==true){
  let queID = {"question_id":q.answer_type.id}
  this.queVar.push(queID);

  this.questionArray=JSON.stringify(this.queVar);
console.log(this.questionArray)
}
else{
  let queID = {"question_id":q.answer_type.id}

  this.queVar.splice(this.queVar.indexOf(queID), 1);
  this.questionArray=JSON.stringify(this.queVar);
console.log(this.questionArray)
}

}



