import { Component, OnInit, ViewChild ,ViewContainerRef} from '@angular/core';
import { Http,Headers } from '@angular/http'
import { DashboardService } from '../dashboard.service';
import { Observable } from 'rxjs';
import { HttpClient,HttpParams } from '@angular/common/http';
import {ActivatedRoute } from "@angular/router";
import { SessionStorageService } from 'ngx-webstorage';
import { Router } from '@angular/router';
import { FlashMessage } from 'angular-flash-message/dist';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { CookieService } from 'ngx-cookie-service';
import { ZiggeoModule } from 'angular-ziggeo/build/ziggeo/index';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { ManageTeamService } from '../manage-team.service';

@Component({
  selector: 'app-manage-team',
  templateUrl: './manage-team.component.html',
  styleUrls: ['./manage-team.component.css']
})
export class ManageTeamComponent implements OnInit {
Aboutuser:any;
manageTeam:any;
teamUpdateRes:any;
preRoleId:any;
userDetails:any;
RoleUserId:any;
AllUsers:any;
userData:any;

  constructor(
    private http:Http,
    private dashboardservice:DashboardService,
    private route:ActivatedRoute,
    private sessionSt:SessionStorageService,
    private router:Router,
    private flashmessages:FlashMessage,
    public toastr:ToastsManager,
    private cookieService:CookieService,
    private spinnerService: Ng4LoadingSpinnerService,
    private teamService:ManageTeamService,
    public vcr: ViewContainerRef,


  ) {  this.spinnerService.show();
       this.toastr.setRootViewContainerRef(vcr);
}

  ngOnInit() {
    this.Aboutuser=this.sessionSt.retrieve("aboutuser");
    this.manageTeam=this.sessionSt.retrieve("allusers");

  }

manageTeamRole(role){
  this.teamService.manageTeamUpdateApi(role).subscribe(
      data=> { this.teamUpdateRes=data;
        this.teamUpdateRes=(data)
        console.log(this.teamUpdateRes);
        this.route.params.subscribe(value => {
        value=this.teamUpdateRes;
        this.sessionSt.store('ManageTeamUpdate',this.teamUpdateRes)

        if(this.teamUpdateRes.status==200){
this.toastr.success(this.teamUpdateRes.message,"Thank You..." )
  this.dashboardservice.getAllUsersData().subscribe(
              data=> { this.AllUsers=data;
                this.AllUsers=(data)
                console.log(this.AllUsers);
                this.route.params.subscribe(value => {
                value=this.AllUsers;
                this.sessionSt.store('AllUsers',this.AllUsers)
 location.reload();
              }
          );
          this.userData=this.sessionSt.retrieve("AllUsers");

        });





        }


        else{
          this.toastr.info(this.teamUpdateRes.message,"Sorry..." )
        }

      }
  );
  this.teamUpdateRes=this.sessionSt.retrieve("ManageTeamUpdate");

});


}


onItemClick(event,manage,questionCheck){
  console.log(manage.role.id);
  console.log(manage.user.id);

  this.preRoleId=manage.role.id;
  this.RoleUserId=manage.user.id;

  this.route.params.subscribe(value => {
  value=this.preRoleId;
  this.sessionSt.store('preRoleId',this.preRoleId)
  this.sessionSt.store('RoleUserId',this.RoleUserId)
}
);

}
}



