import { LoginComponent } from './login/login.component';
import { Routes, RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { ModuleWithProviders } from '@angular/core';
import{ SignupComponent  } from './signup/signup.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ManageTeamComponent } from './manage-team/manage-team.component';
import { QuestionComponent } from './question/question.component';
import { ManageCustomerComponent } from './manage-customer/manage-customer.component';
import { CreateJoygramComponent } from './create-joygram/create-joygram.component';


export const router: Routes = [
    { path: '', redirectTo: 'login', pathMatch: 'full' },
    { path: 'login/:id', component: LoginComponent  },
    { path: 'signup', component: SignupComponent },
    { path: 'dashboard', component: DashboardComponent  },
    { path: 'dashboard/manage-team', component: ManageTeamComponent  },
    {path:'dashboard/create-questions', component: QuestionComponent  },
    {path:'dashboard/manage-customers', component: ManageCustomerComponent },
    {path:'dashboard/create-joygram', component:  CreateJoygramComponent },

    // { path: 'listings', component: ListingsComponent ,   canActivate: [AuthGuard]},
    // { path: 'listing/:id', component: ListingComponent , canActivate: [AuthGuard]},
    // { path: 'addlisting', component: AddlistingComponent,canActivate: [AuthGuard]},
    // { path: 'signup', component: SignupComponent },
    // { path: 'login-email', component: EmailComponent },
    // { path: 'members', component: MembersComponent, canActivate: [AuthGuard] }

]
export const routes: ModuleWithProviders = RouterModule.forRoot(router);



