import { Injectable ,OnInit } from '@angular/core';
import { Http ,Response} from '@angular/http';
import { HttpModule } from '@angular/http';
import { HttpClient,HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Headers } from '@angular/http';
import { Router ,Resolve } from '@angular/router'
import  'rxjs/add/operator/map';
import {ActivatedRoute} from "@angular/router";
import { SessionStorageService } from 'ngx-webstorage';
import { FlashMessage } from 'angular-flash-message/dist';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { CookieService } from 'ngx-cookie-service';

@Injectable()
export class CreateJoygramService {
companyID:any;
videoToken:any;
joygram_title_event:any;
questionArray:any;
causes:any;
hyperlinks_url_label:any;
hyperlinks_url_link:any;
hyperlinks_description:any;
registername:any;
registeremail:any;
selectedOnlyId:any;
charityArraycheck:any;
joygram_message_event:any;
hyperUrlsCheck:any;
tags:any='';
gratitude_card:any;

  constructor(  private http:Http,
    private route:ActivatedRoute,
    private sessionSt:SessionStorageService,
    public router:Router,
    private flashmessages:FlashMessage,
    private cookieService:CookieService,
    public toastr:ToastsManager,
  ) {  }

    ngOnInit() {
      this.companyID=this.sessionSt.retrieve("companyID");

    }
    // 
    // TagsApi(){
    //
    //     this.companyID=this.sessionSt.retrieve("companyID");
    //
    //
    //
    // }

sentjoygramAPI(){
  this.companyID=this.sessionSt.retrieve("companyID");
  this.videoToken=this.sessionSt.retrieve("videoToken");
  this.joygram_title_event=this.sessionSt.retrieve("joygram_title_event");
  this.questionArray=this.sessionSt.retrieve("questionArray");
  this.joygram_message_event=this.sessionSt.retrieve("joygram_message_event");

  this.causes=this.sessionSt.retrieve("causes");
  this.hyperlinks_url_label=this.sessionSt.retrieve("hyperlinks_url_label");
  this.hyperlinks_url_link=this.sessionSt.retrieve("hyperlinks_url_link");
  this.hyperlinks_description=this.sessionSt.retrieve("hyperlinks_description");
  this.registername=this.sessionSt.retrieve("registername");
  this.registeremail=this.sessionSt.retrieve("registeremail");
  this.selectedOnlyId=this.sessionSt.retrieve("selectedOnlyId");
  this.charityArraycheck=this.sessionSt.retrieve("charityArraycheck");
  this.hyperUrlsCheck=this.sessionSt.retrieve("hyperUrlsCheck");
  this.gratitude_card=this.sessionSt.retrieve("gratitude_card");



console.log(this.selectedOnlyId)

let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
  let options = { headers, observe: 'response' };
      const body3 = new HttpParams()

      .set('company_id',this.companyID)
      .set('tags',this.tags)
      .set('message',this.joygram_message_event)
      .set('video_token',this.videoToken)
      .set('event_title',this.joygram_title_event)
      .set('slug_name','devops')
      .set('questions',this.questionArray)
      .set('user_email',this.registeremail)
      .set('user_name',this.registername)
      .set('memberships',this.selectedOnlyId)
      .set('add_gift_card',this.charityArraycheck)
      .set('gratitude_card',this.gratitude_card)
      .set('causes',this.causes)
      .set('is_hyperlinks_included',this.hyperUrlsCheck)
      .set('hyperlink_url_label',this.hyperlinks_url_label)
      .set('hyperlinks_url_link',this.hyperlinks_description)
      .set('hyperlinks_description',this.hyperlinks_url_link)
console.log(body3)

    return this.http.post("https://thawing-depths-48088.herokuapp.com/create_company_joygram.json",body3.toString(),options).map((response:Response)=>response.json());

}
}



