import { Component, OnInit, ViewChild ,ViewContainerRef} from '@angular/core';
import { Http,Headers } from '@angular/http'
import { DashboardService } from '../dashboard.service';
import { Observable } from 'rxjs';
import { HttpClient,HttpParams } from '@angular/common/http';
import {ActivatedRoute } from "@angular/router";
import { SessionStorageService } from 'ngx-webstorage';
import { Router } from '@angular/router';
import { FlashMessage } from 'angular-flash-message/dist';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { CookieService } from 'ngx-cookie-service';
import { ZiggeoModule } from 'angular-ziggeo/build/ziggeo/index';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { ManageTeamService } from '../manage-team.service';
import { QuestionService } from '../question.service';

@Component({
  selector: 'app-question',
  templateUrl: './question.component.html',
  styleUrls: ['./question.component.css']
})
export class QuestionComponent implements OnInit {
  Aboutuser:any;
  manageTeam:any;
  teamUpdateRes:any;
  preRoleId:any;
userDetails:any;
RoleUserId:any;
AllUsers:any;
userData:any;
QuestionData:any;
questionRes:any;
questionName:any;
questionType:any;
AllQuestions:any;

  constructor(
    private http:Http,
    private dashboardservice:DashboardService,
    private route:ActivatedRoute,
    private sessionSt:SessionStorageService,
    private router:Router,
    private questionService:QuestionService,
    private flashmessages:FlashMessage,
    public toastr:ToastsManager,
    private cookieService:CookieService,
    private spinnerService: Ng4LoadingSpinnerService,
    private teamService:ManageTeamService,
    public vcr: ViewContainerRef,


  ) {  this.spinnerService.show();
    this.toastr.setRootViewContainerRef(vcr);
}
  ngOnInit() {
    this.Aboutuser=this.sessionSt.retrieve("aboutuser")
    this.manageTeam=this.sessionSt.retrieve("allusers");

  this.QuestionData=this.sessionSt.retrieve("questionsData");


  }
createQuestion(question,type){
  console.log(question.value);
  this.questionName=question.value.question_name;
  this.questionType=question.value.question_type;

  this.route.params.subscribe(value => {
  value=this.questionName;
  this.sessionSt.store('questionName',this.questionName)
  this.sessionSt.store('questionType',this.questionType)
});

// API call
  this.questionService.createQuestionApi().subscribe(
      data=> { this.questionRes=data;
        this.questionRes=(data)
        console.log(this.questionRes);


        this.route.params.subscribe(value => {
        value=this.questionRes;
        this.sessionSt.store('questionRes',this.questionRes)

        if(this.questionRes.status==200){
      this.toastr.success(this.questionRes.message,"Thank You..." )
      this.dashboardservice.getAllQuestions().subscribe(
              data=> { this.AllQuestions=data;
                this.AllQuestions=(data)
                console.log(this.AllQuestions);
                this.route.params.subscribe(value => {
                value=this.AllQuestions;
                this.sessionSt.store('questionsData',this.AllQuestions)
      location.reload();
              }
          );
          this.QuestionData=this.sessionSt.retrieve("QuestionData");

        });





        }


        else{
          this.toastr.info(this.questionRes.message,"Sorry..." )
        }

      }
      );
      this.questionRes=this.sessionSt.retrieve("questionRes");


      });







}
}



